<template>
    <svg :class="svgClass" aria-hidden="true">
        <use :xlink:href="iconName"/>
    </svg>
</template>

<script>
	export default {
		name: 'bp-svg-icon',
		props: {
			iconClass: {
				type: String,
				required: true,
                default: "case"
			},
			className: {
				type: String,
				default: ''
			}
		},
		computed: {
			iconName () {
				// return `#icon-${this.iconClass}`
				return `#${this.iconClass}`
			},
			svgClass () {
				if (this.className) {
					return 'svg-icon ' + this.className
				} else {
					return 'svg-icon'
				}
			}
		}
	}
</script>

<style scoped>
    .svg-icon {
        width: 1em;
        height: 1em;
        vertical-align: -0.15em;
        fill: currentColor;
        overflow: hidden;
        margin-right: 4px;
    }
</style>
