<template>
<div>
    <button
        class="bp-button"
        @click="handleClick"
        @dblclick="handleDBClick"
        :disabled="buttonDisabled"
        :type="type"
        :class="[
            type ? 'button-' + type : 'button-primary',
            density ? 'button-density-' + density : 'button-density-default',
            {'btn-block': block}
        ]"
    >
        <svg class="icon" width="200" height="200" viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'svg-icon'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M376.717 725.333h270.566C664.855 675.618 712.268 640 768 640c70.692 0 128 57.308 128 128 0 70.692-57.308 128-128 128-55.732 0-103.145-35.618-120.717-85.333H376.717C359.145 860.382 311.732 896 256 896c-70.692 0-128-57.308-128-128 0-70.692 57.308-128 128-128 55.732 0 103.145 35.618 120.717 85.333zM192 128h554.667c35.346 0 64 28.654 64 64 0 35.346-28.654 64-64 64H192c-35.346 0-64-28.654-64-64 0-35.346 28.654-64 64-64zm213.333 256H832c35.346 0 64 28.654 64 64 0 35.346-28.654 64-64 64H405.333c-35.346 0-64-28.654-64-64 0-35.346 28.654-64 64-64z"/></svg>
        {{text}}
    </button>

</div>
</template>

<script>
// import '../assets/css/variables.scss'
// import '../assets/css/addon.scss'
// import '../assets/css/_base.scss'
// import '../icons';
// import "./bp-svg-icon"
export default {
    props: {
        text: String,
        type: String, // standard, subtle, destructive, warning,
        density: String, // comfy, default, compact
        block: Boolean, //button是否占满一行
        disabled: Boolean, //加了disabled就是disabled
        icon: {
            type: String,
            default: ""
        },
    },
	computed: {
        buttonDisabled() {
            return this.disabled;
        },
    },
    methods: {
        handleClick(data) {
            var that = this
            // 开启延时器，300ms的间隔区分单击和双击，解决双击时执行两次单击事件
            clearTimeout(that.time)
            that.time = setTimeout(() => {
                // 写单击事件执行的逻辑代码
                console.log("单击事件")
                this.$emit('click', data);
            },300)
        },
        handleDBClick(data) {
            clearTimeout(this.time)
            console.log("双击事件")
            this.$emit('dbclick', data)

        }
    }
}
</script>

